import useFetch from '../Hooks/fetch';
import { useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { clearProducts, setProducts } from '../Redux/Reducers/products';
import { clearProductsGallery, setChangingPage, setMainPageLoaded, setProductsLength } from '../Redux/Reducers/lifecycle';
import { gsap } from 'gsap';

export default function useProducts() {
	const { getData } = useFetch();
	const dispatch = useDispatch();
	const products = useSelector((state) => state.products);
	const loaded = useSelector((state) => state.lifecycle.images.products.loaded);
	const imagesArray = useRef([]);
	imagesArray.current = [];

	useEffect(() => {
		getData('/products?populate=*').then((res) => {
			dispatch(
				setProducts(
					res.data
						.map((el) => {
							if (el && el.attributes.image.data != null) {
								let image = { ...el.attributes.image.data.attributes, id: el.attributes.image.data.id };
								return {
									...el.attributes,
									id: el.id,
									image: image,
								};
							}
						})
						.filter((el) => el != null)
				)
			);
			dispatch(setMainPageLoaded());
			setTimeout(() => {
				dispatch(setChangingPage(false));
			}, 300);
		});
		return () => {
			dispatch(clearProducts());
			dispatch(clearProductsGallery());
		};
	}, []);

	useEffect(() => {
		loaded && console.log('loaded');
		loaded && console.log('imagesarrayL: ', imagesArray.current);
		gsap.set(imagesArray.current, {
			y: -30,
			x: -15,
		});
		loaded && gsap.to(imagesArray.current, { opacity: 1, y: 0, x: 0, delay: 0.4, duration: 0.15, stagger: { each: 0.03 }, ease: 'circ' });
	}, [loaded]);

	useEffect(() => {
		// products && console.log('products: ', products);
		products && dispatch(setProductsLength(products.length));
	}, [products]);

	const addToRefArray = (el) => {
		if (el && !imagesArray.current.includes(el)) {
			imagesArray.current.push(el);
		}
	};

	return { addToRefArray };
}
