import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setChangingPage, setheaderLoaded } from '../Redux/Reducers/lifecycle';
import { useNavigate } from 'react-router-dom';

export default function useHeader() {
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const [showMobile, setShowMobile] = useState(false);
	useEffect(() => {
		setTimeout(() => {
			dispatch(setheaderLoaded());
		}, 300);
	}, []);

	const handleNavigate = (adress) => {
		if (window.location.pathname != adress) {
			dispatch(setChangingPage(true));
			navigate(adress);
		}
	};

	const showMobileNav = () => {
		setShowMobile(true);
	};

	const hideMobileNav = () => {
		setShowMobile(false);
	};

	return { handleNavigate, showMobile, showMobileNav, hideMobileNav };
}
