import React from 'react';
import './index.css';
import useHeader from '../../Application/Controller/header';
import NavElement from './navElement';
import MobileNav from './mobileNav';

export default function Header() {
	const menu = [
		{ name: 'fired wood', link: '/' },
		{ name: 'unsere produkte', link: '/products' },
		{ name: 'shou sugi ban', link: '/shousugiban' },
		{ name: 'contacten', link: '/contact' },
	];
	const { handleNavigate, showMobile, showMobileNav, hideMobileNav } = useHeader();
	return (
		<header>
			<div className='logo'>firedwood.de</div>
			<ul className='main-nav'>
				{menu.map((el) => {
					return (
						<NavElement
							key={el.name}
							name={el.name}
							navigate={() => {
								handleNavigate(el.link);
							}}
						/>
					);
				})}
			</ul>
			{showMobile && <MobileNav hide={hideMobileNav} menu={menu} handleNavigate={handleNavigate} />}
			<div className='hamburger' onClick={showMobileNav}>
				<div />
				<div />
				<div />
			</div>
		</header>
	);
}
